
body,
html {
  height: 100%;
  font-family: 'Risque';
  overflow: hidden;
}
body {
  margin: 0;
  padding: 10px;
  box-sizing: border-box;
  background: white;
}
#app {
  border: 1px solid black;
  background: #A1FFCE;
  background: linear-gradient(to right, #FAFFD1, #A1FFCE);
  height: 100%;
}
button:focus {
  outline: 0;
}
.centerContent {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.fullHeight {
  height: 100%;
}
a {
  text-decoration: none;
}



